import variables from '../../../assets/styles/variables';

const NotificationListStyles = {
    maxWidth: '100vw',
    width: '400px',
    position: 'fixed',
    padding: `0 ${variables.paddingSmall}`,
    bottom: 0,
    left: '50%',
    zIndex: 4,
    transform: 'translateX(-50%)',
};

export default NotificationListStyles;


